import React from "react";
import { Link } from "react-router-dom";

import { news } from "../config/config";

import Title from "../components/Title";

import "../styles/news.css";

export default class News extends React.Component {

    state = {
        category: 1,
        articles: []
    }

    constructor() {
        super();

        this.loadData = this.loadData.bind(this);
    }

    loadData() {
        var articles = []

        for (let i = 0; i < news.length; i++) {
            if (news[i].type === this.state.category) {
                articles.push(news[i]);
            }
        }

        this.setState({ articles: articles })
    }

    componentDidMount() {
        this.loadData();
    }

    render() {
        return(
            <div className="screen" id="news">
                <Title title="Novinky TerraMia" image="item-2.png" />

                <div className="content" style={this.state.articles.length === 0 ? { alignItems: "center" } : null}>
                    <div className="menu-panel">
                        <div className="title">Kategórie</div>

                        <div className="menu">
                            <div className="item" onClick={() => this.setState({ category: 1 }, () => this.loadData())}>Novinky<ion-icon name="chevron-forward-outline"></ion-icon></div>
                            <div className="item" onClick={() => this.setState({ category: 2 }, () => this.loadData())}>Udalosti<ion-icon name="chevron-forward-outline"></ion-icon></div>
                            <div className="item" onClick={() => this.setState({ category: 3 }, () => this.loadData())}>Akcie<ion-icon name="chevron-forward-outline"></ion-icon></div>
                            <div className="item" onClick={() => this.setState({ category: 4 }, () => this.loadData())}>Kurzy<ion-icon name="chevron-forward-outline"></ion-icon></div>
                            <div className="item" onClick={() => this.setState({ category: 5 }, () => this.loadData())}>Prednášky<ion-icon name="chevron-forward-outline"></ion-icon></div>
                            <div className="item" onClick={() => this.setState({ category: 6 }, () => this.loadData())}>Semináre<ion-icon name="chevron-forward-outline"></ion-icon></div>
                        </div>
                    </div>

                    <div className="list-panel">
                        <div className="list">
                            {this.state.articles.map((article) => <Article article={article} />)}
                        </div>

                        {this.state.articles.length === 0 ? <div className="empty-message">Nenašli sa žiadne novinky v danej kategórií</div> : null}
                    </div>
                </div>
            </div>
        )
    }
}

function Article(props) {
    const article = props.article;

    return(
        <Link className="article" to={"/novinky/" + article.id}>
            <img className="image" src={article.image} />
            <div className="title">{article.title}</div>
            <div className="date">{article.date}</div>
            <div className="text">{article.text}</div>

            <div className="button-filled">Čítaj viac</div>
        </Link>
    )
}